<template>
  <div class="handle">
    <el-card class="handle-wrapper">
      <div slot="header" class="clearfix">
        <span class="text">库存管理</span>
        <el-button style="float:right;" class="back" type="danger" @click="back">返回</el-button>
      </div>
      <div class="handle-item">
        库存编号：{{ storeId }}
      </div>
      <div class="handle-item">
        样板编号：{{ templateId }}
      </div>
      <div class="handle-item">
        样板数量：{{ templateAmount }}
      </div>
      <div class="handle-item">
        样板分厂：{{ templateFactory }}
      </div>
      <div class="handle-item">
        样板产区：{{ templateAreas }}
      </div>
      <div class="handle-item">
        样板存放点：{{ templateStore }}
      </div>
      <div class="handle-item">
        <el-input placeholder="调入数量" v-model="handleInAmount" :size="size" class="handle-input"></el-input>
        <el-button type="primary" @click="handleIn" :size="size" class="handle-button">样板调入</el-button>
      </div>
      <div class="handle-item">
        <el-input placeholder="调出数量" v-model="handleOutAmount" :size="size" class="handle-input"></el-input>
        <el-button type="primary" @click="handleOut" :size="size" class="handle-button">样板调出</el-button>
      </div>
      <div class="handle-item">
        <el-input placeholder="外借分厂" v-model="handleLoanFactory" :size="size" class="handle-input"></el-input>
        <el-input placeholder="外借数量" v-model="handleLoanAmount" :size="size" class="handle-input"></el-input>
        <el-button type="primary" @click="handleLoan" :size="size" class="handle-button">样板外借</el-button>
      </div>
    </el-card>
  </div>
</template>

<script>
  import * as api from 'api/index'
  export default {
    name: "",
    data() {
      return {
        size: 'medium',
        handleInAmount: '',
        handleOutAmount: '',
        handleLoanAmount: '',
        handleLoanFactory: '',
        storeId: '',
        templateId: '',
        templateAmount: '',
      }
    },
    methods: {
      _isPositiveInteger(s) {
        var re = /^[1-9]+[0-9]*]*$/
        return re.test(s)
      },
      handleIn() {
        if (this.handleInAmount === '') {
          this.$message.error('请输入调入数量')
        }
        else if (!this._isPositiveInteger(this.handleInAmount)) {
          this.$message.error('请输入正确的数字')
        }
        else {
          let handleIn = {
            id: this.storeId,
            templateId: this.templateId,
            templateAmount: this.handleInAmount,
            templateAreas: this.templateAreas,
            templateStore: this.templateStore
          }
          var t = JSON.stringify(handleIn)
          // console.log(t)
          api.HandleIn.r(t).then(res => {
            this.handleInAmount = ''
            this._updateHandle(this.storeId)
            this.$message.success('调入成功')
          })
        }
      },
      handleOut() {
        if (this.handleOutAmount === '') {
          this.$message.error('请输入调出数量')
        }
        else if (!this._isPositiveInteger(this.handleOutAmount)) {
          this.$message.error('请输入正确的数字')
        }
        else {
          let handleOut = {
            id: this.storeId,
            templateId: this.templateId,
            templateAmount: this.handleOutAmount,
            templateAreas: this.templateAreas,
            templateStore: this.templateStore
          }
          var t = JSON.stringify(handleOut)
          api.HandleOut.r(t).then(res => {
            this.handleOutAmount = ''
            this._updateHandle(this.storeId)
            this.$message.success('调出成功')
          }).catch(err => {
            console.log(err)
          })
        }
      },
      handleLoan() {
        /*if (this.handleLoanFactory === '') {
          this.$message.error('请输入分厂信息')
        }*/
        if (this.handleLoanAmount === '') {
          this.$message.error('请输入外借数量')
        }
        else if (!this._isPositiveInteger(this.handleLoanAmount)) {
          this.$message.error('请输入正确的数字')
        }
        else {
          let handleLoan = {
            id: this.storeId,
            templateId: this.templateId,
            // templateFactory: this.templateFactory,
            templateAmount: this.handleLoanAmount,
            templateAreas: this.templateAreas,
            templateStore: this.templateStore
          }
          var t = JSON.stringify(handleLoan)
          api.HandleLoan.r(t).then(res => {
            this.handleLoanAmount = ''
            this._updateHandle(this.storeId)
            this.$message.success('外借成功')
          })
        }
      },
      back() {
        let id = this.$route.query.tid
        let areas = this.$route.query.areas
        let factory = this.$route.query.factory
        let store = this.$route.query.store
        let currentPage = this.$route.query.currentPage
        this.$router.push(`/store?id=${id}&areas=${areas}&factory=${factory}&store=${store}&currentPage=${currentPage}`)
      },
      _updateHandle(id) {
        api.HandleInfo.r(id).then(res => {
          if (res.data.success === 'SUCCESS') {
            console.log(res)
            this.storeId = res.data.data.id
            this.templateId = res.data.data.templateId
            this.templateAmount = res.data.data.templateAmount
            this.templateFactory = res.data.data.templateFactory
            this.templateAreas = res.data.data.templateAreas
            this.templateStore = res.data.data.templateStore
          }
        })
      }
    },
    created() {
      this.size = (window.innerWidth < 767) ? 'mini' : 'medium'
      
      /* 这里要获取 this.$route.query.templateId */
      let id = this.$route.query.id
      this._updateHandle(id)
      /*api.HandleInfo.r(id).then(res => {
        if (res.data.success === 'SUCCESS') {
          this.storeId = res.data.data.id
          this.templateId = res.data.data.templateId
          this.templateAmount = res.data.data.templateAmount
        }
      })*/
    }
  }
</script>

<style type="text/stylus" lang="stylus" rel="stylesheet/stylus" scoped>
  @import "~assets/stylus/variable.styl"
  @import "Handle.styl"
</style>



